const mongoose = require('mongoose');
import autoIncrement from 'mongoose-auto-increment';
const {
    ObjectId
} = mongoose.Schema;

const SimSimSchema = mongoose.Schema({
    text: {
        type: String,
        required: true,
    },
    user: {
        type: ObjectId,
        ref: 'User',
    },
    room: {
        type: ObjectId,
        ref: 'SimSimRoom',
    },
    bot: {
        type: Boolean,
        default: false,
    },
    filterCh: {
        type: Boolean,
        default: false,
    },
    serviceCode: {
        type: String,
        default: '0'
    },

    date: {
        type: Date,
        default: Date.now(),
    },
});
autoIncrement.initialize(mongoose.connection);
SimSimSchema.plugin(autoIncrement.plugin, {
    model: 'SimSim',
    field: 'id',
    startAt: 1,
    incrementBy: 1,
    index: true,
});

const model = mongoose.model('SimSim', SimSimSchema);

export default model;